import React, { useState } from 'react';

import Button from '@mui/material/Button';
import SendIcon from '@mui/icons-material/Send';
import Rating from '@mui/material/Rating';
import Box from '@mui/material/Box';
import LinearProgress from '@mui/material/LinearProgress';
import Alert from '@mui/material/Alert';

/*  */

function Review() {

    //

    const [inputText, setInputText] = useState('');  //
    const [textList, setTextList] = useState([]);  //  

    const updateList = () => {
        const newList = [...textList, inputText];  //...- 

        if (inputText !== '') {
            setTextList(newList);
            setInputText('');  //form
        }
        else {
        }
    }

    const [value, setValue] = React.useState(0);  //

    const list = textList.map(
        (text, i) => <p>{i + 1} : {text}</p>
    );

    return (
        <div>
            <Alert severity="warning" color="error">
                
            </Alert>
            <Box sx={{ width: '100%' }}>
                <LinearProgress />
            </Box>
            <div class="bg-white border p-3 m-5" >
                <div class="row">
                    <h2 class="pb-2">KeyCaps</h2>
                    <Box sx={{ '& > legend': { mt: 2 }, }}>
                        <Rating name="simple-controlled" value={value} onChange={(event, newValue) => { setValue(newValue); }} />
                    </Box>
                    <div class="col-md-5">
                        <div class="form-floating pb-3">
                            <textarea class="form-control w-75" placeholder="Leave a comment here" style={{ height: 150 }} value={inputText} onChange={(e) => setInputText(e.target.value)} />
                            {/* onChange={(e) => setInputText(e.target.value)} goods.jshandleChangeesetInputText */}
                            <label for="floatingTextarea"></label>
                        </div>
                        <Button variant="outlined" endIcon={<SendIcon />} onClick={updateList}></Button>
                    </div>
                    <div class="col-md-6 border rounded">
                        <div className="print">
                            <p class="my-2 mx-1">{list}</p>
                        </div>
                    </div>
                </div>
                <p class="py-5" />
            </div>
        </div>
    );
}

export default Review;